@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}
.App {
  height: 100%;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Header */
.header__wrapper {
  display: flex;
  background: #111111;
  color: #ffffff;
  height: 100%;
}
.header__title {
  font-size: 28px;
  font-weight: 600;
  margin: auto;
  justify-content: center;
}

/* Footer */
.footer__wrapper {
  display: flex;
  background: #111111;
  color: #ffffff;
  height: 100%;
}

/* Sidebar */
.sidebar__wrapper {
  background: #666666;
  color: #ffffff;

  height: 100%;
  margin: 0;
  padding: 10px;
}

.sidebar__wrapper > p {
  margin: 10px;
}

.sidebar__wrapper > ul {
  margin: 0;
  padding-left: 20px;
  list-style-type: none;
  list-style-position: outside;
  text-align: left;
}

.sidebar__wrapper a {
  color: #ffffff;
  text-decoration: none;
}

/* Content */
.content__wrapper {
  height: 100%;
}

/* Image Uploader */
.image-uploader__button {
  font-family: inherit;
  font-weight: 600 !important; /* override the default style */
}

/* Google Map */
.google-map {
  margin: auto;
  padding: 10px;
  
  height: 60%;
  width: 80%;
}

.info-window .place-name {
  font-size: 14px;
}
.tripago__wrapper {
  height: 100%;
  display: grid;
  grid-template-columns: 200px auto;
  grid-template-rows: 50px auto;
}

.tripago__header {
  grid-column: 1 / -1;
  grid-row: 1;
}

.tripago__sidebar {
  grid-column: 1;
  grid-row: 2 / -1;
}

.tripago__content {
  grid-column: 2 / -1;
  grid-row: 2 / -1;
}
